import React from 'react';
import ProjectGenerator from './ProjectGenerator/ProjectGenerator';
import './Projects.css';

const Projects = (props) => {
	let to_render = [];
	let content = [];
	let projectsClass = '';
	if(props.language === 'English') {
		content = props.contents;
		projectsClass = 'projects-en';
	}
	else {
		content = props.contents_zh;
		projectsClass = 'projects-zh';
	}
	for (let item in content) {
		to_render.push(
			<div className = {projectsClass}>
				<h3>{content[item][0].display_name}</h3>
				<ProjectGenerator projects = {content[item]}/>
			</div>
		);	
	}
	
	return (
		<div key = 'Projects_table'>
			<div className = 'column big left extended'>

				{to_render}				

			</div>
			<div className = 'column small right static_others'>
				<img className = 'photo' src = './pics/projects_sidephoto.png' alt = 'error'/>
			</div>
		</div>
	);

}

export default Projects;