import React from 'react';
import './ProjectGenerator.css';

const ProjectGenerator = (props) => {
	
	let content = props.projects;
	let temp = [];
	let to_return = [];
	for (let index = 0 ; index < content.length ; index ++) {
		
		if (temp[content[index].department] !== undefined) {
			temp[content[index].department].push(content[index]);
		}
		else temp[content[index].department] = [content[index]];
	}

	for (let category in temp) {
		let to_append = temp[category].map((item) => 
			<li key = {item.description}>
				{item.title} <b>{item.index}</b> 
			</li>
		)
		to_return.push(
			<div>
				<h4>{temp[category][0].department}</h4>
				<ol>{to_append}</ol>
			</div>
		);
	}
	
	return to_return;
}

export default ProjectGenerator;