import React from 'react';
import './Publications.css';

const publicationGenerator = (content, language) => {
	let temp = [];
	let publicationClass = '';
	if (language === 'English') publicationClass = 'publications-en';
	else publicationClass = 'publications-zh';
	for (let category in content) {
		let to_append = content[category].map((item) => 
			<li key = {item.description}>
				{item.title} <b>{item.index}</b> 
				{item.file_path !== undefined ? 
					<a target = "_blank" href = {item.file_path} rel="noopener noreferrer">
						<img className = 'pdf_icon' src = './pics/pdf-icon.png' alt = 'icon'/>
					</a> : null}
			</li>
		)
		temp.push(
			<div className = {publicationClass}>
				<h3>{content[category][0].display_name}</h3>
				<ol className = 'publications_content'>{to_append}</ol>
			</div>
		);
	}
	return temp;
}

const Publications = (props) => {
	
	return (
		<div>
			<div className = 'column big left extended'>
				{props.language === 'English' ? publicationGenerator(props.contents, props.language) : publicationGenerator(props.contents_zh, props.language)}
			</div>
			
			<div className = 'column small right static_publications'>
				<img className = 'photo block' src = './pics/publications_sidephoto.png' alt = 'error' />
				<a className = 'google_scholar block' href="https://goo.gl/rxy6Ef" target="_blank" rel="noopener noreferrer">
					<img src="pics/google-scholar-with-link.png" alt = 'googleScholar'/>
				</a>
			</div>
		</div>
	);
}

export default Publications;