import React from 'react';
import './ServicesGenerator.css';

const ServicesGenerator = (props) => {
	
	let content = props.services;
	let temp = [];
	let to_return = [];

	for (let index = 0 ; index < content.length ; index ++) {
		
		if (temp[content[index].location] !== undefined) {
			temp[content[index].location].push(content[index]);
		}
		else temp[content[index].location] = [content[index]];
	}

	for (let category in temp) {
		let to_append = temp[category].map((item) => 
			<li key = {item.description}>
				{item.service} <b>{item.index}</b> 
			</li>
		)
		to_return.push(
			<div>
				<h4>{temp[category][0].location}</h4>
				<ol>{to_append}</ol>
			</div>
		);
	}
	
	return to_return;
}

export default ServicesGenerator;