import React from 'react';
import ServicesGenerator from './ServicesGenerator/ServicesGenerator';
import './Services.css';

const Services = (props) => {
	let to_render = [];
	let content = [];
	let servicesClass = '';
	if (props.language === 'English') {
		content = props.contents;
		servicesClass = 'services-en';
	}
	else {
		content = props.contents_zh;
		servicesClass = 'services-zh';
	}
	
	for (let item in content) {
		to_render.push(
			<div className = {servicesClass}>
				<h3>{content[item][0].display_name}</h3>
				<ServicesGenerator services = {content[item]}/>
			</div>
		);
	}

	return (
		<div>
			<div className = 'column big left extended'>

				{to_render}				

			</div>
			<div className = 'column small right static_others'>
				<img className = 'photo' src = './pics/services_sidephoto.png' alt = 'error'/>
			</div>
		</div>
	);
}


export default Services;