import React from 'react';
import './TitleAndDropdown.css';

const eng = {
	title: 'Dr. Tzu-Bin Lin\'s Website',
	dropdown: [
		['Profile', 'Profile', './pics/navbar/Profile.png'],
		['Publications', 'Publications', './pics/navbar/Publications.png'],
		['Projects', 'Projects', './pics/navbar/Projects.png'],
		['Team', 'Team', './pics/navbar/Team.png'],
		['Services', 'Services', './pics/navbar/Services.png']
	],
	TitleAndDropdownClass: 'navbar-item',
	titleClass: 'title-en'
}

const zh = {
	title: '林子斌博士的個人網站',
	dropdown: [
		['學經歷', 'Profile', './pics/navbar/Profile.png'],
		['發表著作', 'Publications', './pics/navbar/Publications.png'],
		['研究專案', 'Projects', './pics/navbar/Projects.png'],
		['研究團隊', 'Team', './pics/navbar/Team.png'],
		['社會服務', 'Services', './pics/navbar/Services.png']
	],
	TitleAndDropdownClass: 'navbar-item-zh',
	titleClass: 'title-zh'
}

const TitleAndDropdown = (props) => {
	let content = '';
	if (props.language === 'English') content = eng;
	else content = zh;

	let temp = content.dropdown.map((item) => 
		<p 
			className = {content.TitleAndDropdownClass} 
			onClick = {props.changePage} 
			key = {item[0]} 
			id = {item[1]}>
			<span>{item[0]}</span> <br/><img src = {item[2]} alt = 'alert'/>
		</p>
	)

	return (
		<div>
			<h1 className = {content.titleClass}>{content.title}</h1>
			<div id = 'dropdown'>{temp}</div>
		</div>
	);
}


export default TitleAndDropdown;