import React from 'react';
import './profileOthers.css';


const ProfileOthers = (props) => {
	
	let edu_content = props.edu.map((item) => 
		<p key = {item.description}><strong><em>{item['first']}</em></strong>, {item['second']}</p>
	)
	edu_content.unshift(<h3 id="education" key = 'edu'>{props.edu[0].display_name}</h3>);


	let work_exp_content = props.work.map((item) => 
		<p key = {item.description}>{item['first']}<br />{item['second']}<br/><em>{item['third']}</em></p>
	)
	work_exp_content.unshift(<h3 key = 'work'>{props.work[0].display_name}</h3>);


	let awards_content = props.award.map((item) => 
		<tr key = {item.description}><td>{item['first']}&nbsp;&nbsp;&nbsp;&nbsp;</td><td>{item['second']}</td></tr>
	);


	let research_content = props.research.map((item) => 
		<li key = {item.description}>{item['first']}</li>
	)
		
		
	return (
		<div className = 'profileOthers'>	
			{edu_content}
			{work_exp_content}
			<h3>{props.award[0].display_name}</h3>
			<table><tbody>{awards_content}</tbody></table>
			<h3>{props.research[0].display_name}</h3>
			<ol>{research_content}</ol>
		</div>
	);
}

export default ProfileOthers;