import React from 'react';

import ProfileIntro from './ProfileIntro/ProfileIntro';
import ProfileOthers from './ProfileOthers/ProfileOthers';
import ProfileInfo from './ProfileInfo/ProfileInfo';

const Profile = (props) => {
	let profile_caption = [];
	let profile_intro = [];
	let profile_edu = [];
	let profile_work = [];
	let profile_award = [];
	let profile_research = []; 
	if (props.language === 'English'){
		profile_caption = props.contents.caption;
		profile_intro = props.contents.intro;
		profile_edu = props.contents.edu;
		profile_work = props.contents.work_exp;
		profile_award = props.contents.awards_and_grants;
		profile_research = props.contents.research_interests;
	}
	else {
		profile_caption = props.contents_zh.caption;
		profile_intro = props.contents_zh.intro;
		profile_edu = props.contents_zh.edu;
		profile_work = props.contents_zh.work_exp;
		profile_award = props.contents_zh.awards_and_grants;
		profile_research = props.contents_zh.research_interests;
	}
	return (
		<div>
			<div className = 'column small left static_profile'>
				<ProfileInfo caption = {profile_caption} language = {props.language}/>
			</div>
			<div className = 'column big right extended'>
				<ProfileIntro intro = {profile_intro} id = 'intro_top'/>
				<ProfileOthers 
					edu = {profile_edu} 
					work = {profile_work} 
					award = {profile_award} 
					research = {profile_research}
				/>
			</div>
			
		</div>
	);
}

export default Profile;