import React from 'react';
import TeamGenerator from './TeamGenerator/TeamGenerator';
import './Team.css';


const Team = (props) => {
	let to_render = [];
	let content = [];
	let teamClass = '';
	if (props.language === 'English') {
		content = props.contents;
		teamClass = 'team-en';
	}
	else {
		content = props.contents_zh;
		teamClass = 'team-zh';
	}

	for (let item in content) {
		to_render.push(<h3 className = {teamClass}>{content[item][0].display_name}</h3>);
		to_render.push(<TeamGenerator team = {content[item]}/>);
	}

	return (
		<div>
			<div className = 'column small left static_team'>
				<img className = 'photo' src = './pics/team_sidephoto.png' alt = 'error'/>
			</div>
			<div className = 'column big right extended'>
				{to_render}
			</div>
		</div>
	);
}

export default Team;